export default class ConfigBuilder {
  constructor(config, business) {
    this.config = config;
    if (!business){
      this.alert("urlopt");
    }
    if (!this.config[business]) {
      this.alert(`[${this.config.name}] urlopt`);
    }
    Object.getOwnPropertyNames(this.config[business]).forEach(key => {
      this[key] = this.config[business][key];
    })
  }
  
  alert(msg){
    dsf.layer.alert({
      title: "",
      type: "warning",
      message: msg
    });
    throw new Error(msg)
  }
}