class EventBus {
  constructor() {
    this.events = {};
  }
  
  listen(event, callback){
    if (this.events[event] === undefined){
      this.events[event] = [callback];
    }
    else {
      const arr = this.events[event];
      if (!_.isArray(arr)){
        console.log(` on  ${event} `)
        return
      }
      arr.push(callback)
    }
  }
  dispatch(event, params){
    const eventDeal = this.events[event];
    if (!eventDeal){
      console.log(event + "listen ")
      return;
    }
    
    if (!_.isArray(eventDeal)){
      console.log(event + "on, emit")
      return
    }
    eventDeal.forEach(a => {
      a(params)
    })
  }

  on(event, callback)  {
    if (this.events[event] === undefined){
      this.events[event] = callback
    }
    else if (_.isArray(this.events[event])){
      console.log(` listen  ${event} `);
    }
    else {
      console.log(event + " ");
    }
  }
  emit(event, params) {
    const eventDeal = this.events[event]
    if (!eventDeal){
      console.log(event + "on ")
      return;
    }
    if (_.isArray(eventDeal)){
      console.log(event + "listen, dispatch")
      return
    }
  
    eventDeal(params)
  }
  off(event) {
    this.events[event] = undefined;
  }
  clear(){
    this.events = {}
  }

  $getAllListener(){
    return Object.entries(this.events)
  }
}

const eventBus = new EventBus();
window.$simpleEventBus = eventBus;
export default eventBus